<template>
    <div class="service_conatiner">
        <head-top goBack="true" head-title="服务中心"></head-top>
        <section class="service_body">
            <section class="service_header">
                 <a href="https://ecs-im.ele.me/" class="service_left">
                    <svg>
                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#human"></use>
                    </svg>
                    <span>在线客服</span>
                </a>
                 <a href="tel:10105757" class="service_right">
                <svg>
                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#phone"></use>
                </svg>
                <span>在线客服</span>
                </a>
            </section>
            <section class="service_tx">
                <div class="service_tx_1">
                    <h4>热门问题</h4>
                </div>
            </section>
            <ul class="server_ul" v-if="numlist">
                <li v-for="(item,index) in numtitle" :key="index" @click="shuoming(index,item)">
                    <div class="tx">
                        {{item}}
                    </div>
                    <svg class="arrow-svg" fill="#999">
                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                    </svg>
                </li>
            </ul>
        </section>
        <transition name="queestion_view" mode="out-in">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
import {mapMutations} from 'vuex'
import headTop from '../../components/header/header'
import {getService} from '../../service/getData'
export default {
    components: {
        headTop,
    },
    data () {
        return {
            numtitle:[],
            numtx:[],
            numlist:[]
        }
    },
    mounted () {
        this.initdata();
    },methods: {
        ...mapMutations([
            'SAVE_QUESTION'
        ]),
        async initdata()
        {
            this.numlist=await getService();
            Object.keys(this.numlist).forEach(item=>{
                let boox=false;
                this.numtitle.forEach(item1=>{

                    if(item1==this.numlist[item])
                    {
                        boox=true;
                    }
                })
                
                if(item.indexOf('Caption')!==-1 && !boox)
                {
                    this.numtitle.push(this.numlist[item]);
                    //console.log(this.numtitle);
                }else if(!boox)
                {
                    this.numtx.push(this.numlist[item]);
                }
            })
        },
        shuoming(index,item)
        {
            
            this.SAVE_QUESTION({item,tx:this.numtx[index]});
            this.$router.push('/service/questionDetail');
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../style/mixin.scss';
.service_conatiner{
    padding-top: 2rem;
}
.service_header{
    background: #fff;
    display: flex;
    a{
        border-right: 0.025rem solid #f1f1f1;
        border-bottom: 0.025rem solid #f1f1f1;
        align-items: center;
        text-align: center;
        flex:1;
        display: flex;
        flex-direction: column;
        flex-wrap:wrap;
        padding:1rem 0;
        svg{
            @include wh(1rem,1rem);
        }
        span{
            line-height: 1.5rem;
            @include sc(.5rem,#333);
        }
    }
}
.service_tx{
    background: #fff;
    .service_tx_1{
        border-bottom: 0.025rem solid #f1f1f1;
        padding:0.6rem;
        h4{
            @include sc(0.7rem,#333);
        }
    }
}
.server_ul{
    li{
        background: #fff;
        border:0.025rem solid #f1f1f1;
        padding:0.5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .tx{
            @include sc(0.6rem,#333);
        }
        svg{
            @include wh(.6rem,.6rem);
        }
    }
}
.queestion_view-enter-active,.queestion_view-leave-active{
    transition: all .4s;
}
.queestion_view-enter,.queestion_view-leave-active{
    transform: translate(2rem,0,0);
    opacity: 0;
}
</style>



